import React from 'react';
import { Separator, Cell, Header, CardGrid, Card, Div, platform, IOS, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';


//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //ios
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android


import Icon12Verified from '@vkontakte/icons/dist/12/verified';


const osname = platform();

const News = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
            Новости
        </PanelHeader>
       <Group>
       
       <CardGrid>
          <Card size="l" mode="shadow">
          {<Header mode="secondary">24 апр в 14:50</Header>}
          
          <Div style={{ paddingTop: 1, paddingBottom: 12, color: 'black' }}>
          В Курганской области 24 апреля зарегистрировано два новых случая COVID-19. 
          Это жители Кургана и Далматово, оба контактные.
          <br/>
          <br/>
          Жительница города Далматово в настоящее время транспортируется в 
          областную инфекционную больницу. Устанавливается круг ее контактных лиц,
          проводятся все необходимые профилактические мероприятия.
          <br/>
          <br/>
          Вторая заболевшая - медработник инфекционной больницы. 
          Самоизолирована по месту работы с момента заболевания.
          <br/>
          <br/>
          Всего с начала регистрации заболеваемости в регионе 29 человек, 
          у которых подтвержден диагноз «новая коронавирусная инфекция». 
          4 человека выздоровели и выписаны.
          На 24 апреля лабораториями медучреждений проведено более 6000 исследований. 
          Лаборатории оснащены необходимыми тест-системами.
          <br/>
          <br/>
          Под меднаблюдением находятся более 1000 человек, в том числе 311 контактных с заболевшими.
          </Div>
          <Separator style={{ margin: '1px 0' }} />
          <Cell before={<Icon12Verified width={20} height={20} />}>Официальный источник</Cell>
          
          
            <div style={{ height: 1 }} />
            
          </Card>
        </CardGrid>


       <CardGrid>
          <Card size="l" mode="shadow">
          {<Header mode="secondary">23 апр в 16:53</Header>}
          <Div style={{ paddingTop: 1, paddingBottom: 12, color: 'black' }}>
          На 23 апреля в Курганской области зарегистрировано 27 подтверждённых 
          случаев COVID-19. Семь случаев – за прошедшие сутки, в том числе
           четыре случая, о которых оперативный штаб сообщал вчера в 20 часов вечера.
            Пятеро из вновь заболевших – это жители Курганской области, прибывшие
             из Москвы, ещё – это те, кто были в контакте с заболевшими.
          <br/>
          <br/>
          В отношении граждан, которые прибывают в Курганскую область из Москвы
           и не заражены коронавирусом, Роспотребнадзором выданы постановления 
           о самоизоляции на 14 суток. Такие же документы вручены членам их семей.
            В случае нарушения режима самоизоляции к гражданам будут применяться 
            меры административного воздействия.
          </Div>
          <Separator style={{ margin: '1px 0' }} />
          <Cell before={<Icon12Verified width={20} height={20} />}>Официальный источник</Cell>
            <div style={{ height: 1 }} />
          </Card>
        </CardGrid>


       <CardGrid>
          <Card size="l" mode="shadow">
          {<Header mode="secondary">22 апр в 13:30</Header>}
          <Div style={{ paddingTop: 1, paddingBottom: 12, color: 'black' }}>
          По данным на 22 апреля в Курганской области зарегистрировано 2 новых
           подтвержденных случая COVID-19. Это жители города Кургана и города
            Щучье. Случаи завозные, граждане прибыли из Москвы. Они 
            госпитализированы в Курганскую областную инфекционную больницу.
             Членам их семей Управлением Роспотребнадзора выданы постановления 
             об обязательной самоизоляции сроком на 14 дней.
          <br/>
          <br/>
          Всего с момента начала регистрации заболеваемости в регионе 
          зафиксировано 22 случая новой коронавирусной инфекции. 19 
          человек находятся на лечении в инфекционной больнице.
           3 – выписаны по выздоровлению.
          </Div>
          <Separator style={{ margin: '1px 0' }} />
          <Cell before={<Icon12Verified width={20} height={20} />}>Официальный источник</Cell>
            <div style={{ height: 1 }} />
          </Card>
        </CardGrid>


       <CardGrid>
          <Card size="l" mode="shadow">
          {<Header mode="secondary">17 апр в 11:14</Header>}
          <Div style={{ paddingTop: 1, paddingBottom: 12, color: 'black' }}>
          16 апреля в Курганской области зарегистрирован новый, 13-й, 
          случай COVID-19. Заболевший – мужчина, который приехал в 
          Курган из Москвы. Когда почувствовал себя плохо, обратился 
          за медицинской помощью. Сейчас мужчина госпитализирован в 
          инфекционную больницу. Управлением Роспотребнадзора по 
          Курганской области организовано проведение профилактических 
          мероприятий устанавливается круг контактных лиц.
          <br/>
          <br/>
          Всего в инфекционной больнице на лечении сейчас находятся 
          10 пациентов с диагнозом «новая коронавирусная инфекция». 
          Трое – выписаны по выздоровлению.
          </Div>
          <Separator style={{ margin: '1px 0' }} />
          <Cell before={<Icon12Verified width={20} height={20} />}>Официальный источник</Cell>
            <div style={{ height: 1 }} />
          </Card>
        </CardGrid>
      </Group>
      <br/>
      <br/>
    </Panel>

);

export default News;




