import React from 'react';
import { Cell, Switch, platform, IOS, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';

// иконки

//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //iso
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android

const osname = platform();

const Theme = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="edit">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
         Оформление
        </PanelHeader>
        <Group>
        <Cell asideContent={<Switch />}description="Смена ночной и дневной темы">Тёмная тема</Cell>
    </Group>
    </Panel>
);

export default Theme;