import React from 'react';
import { Avatar, Header, platform, IOS, Cell, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';


//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //ios
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android


const osname = platform();

const Developer = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="info">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
            Разработчики
        </PanelHeader>
       <Group>
       

       {<Header mode="secondary">Разработчик</Header>}
            <Cell before={<Avatar size={50} mode="image" src="https://sun1-19.userapi.com/NBdk8rJennqdQYEj8QcG6AuNd9nJ4mSNMnlUBw/l2PPeBHPt38.jpg" />}description="Страница ВКонтакте"  onClick={props.go} href="https://vk.cc/at1ki5">Егор Гордеев</Cell>
            <Cell before={<Avatar size={50} mode="image" src="https://sun1-17.userapi.com/UJ7nC_afz7dfpDXGE5_YF8hOTn0sKYcVIP8HSA/UfkrgOk6CYM.jpg" />}description="Страница Instagram"  onClick={props.go} href="https://vk.cc/at1kFk">_youn_gordeev_</Cell>
            <Cell before={<Avatar size={50} mode="image" src="https://sun9-36.userapi.com/c858328/v858328488/1842f1/ujqH0jJIRwg.jpg" />}description="Сообщество Администратор"  onClick={props.go} href="https://vk.cc/at1kP1">Dalmatovo 360</Cell>



            


    </Group>

    </Panel>
);

export default Developer;